.workBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    min-height: 80vh;
    width: 100vw;
}

.listBox {
    display:flex;
    justify-content: flex-end;
    align-items: center;
    min-width: 25vw;
    min-height: 70vh;
    margin: 0;
}

.videosBox {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    min-width: 75vw;
}

.listItems {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    min-width: 15vw;
    min-height: 30vh;
    margin-top: 60px;
}

.listItems .everyBtn {
    min-width: 20vw;
    min-height: 6vh;
    cursor: pointer;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    letter-spacing: 2px;
    text-align: left;
}

.appPage .everyBtn {
    border: 1px solid;
    border-left: 1px solid;
    border-right: 1px solid;
    color: white;
    background-color: black;
    border-color: white;
}

.appPage .everyBtn:hover {
    color: black;
    background-color: white;
    border-style: solid;
    border: 1px black;
    transition: 0.5s ease-in-out;
}

.light .everyBtn {
    border: 1px solid;
    border-left: 1px solid;
    border-right: 1px solid;
    color: black;
    background-color: white;
    border-color: black;
}

.light .everyBtn:hover {
    color: white;
    background-color: black;
    border: 1px solid white;
    transition: 0.5s ease-in-out;
}

.videosBox iframe {
    height: 620px;
    width: 800px;
}

#item-1, #item-2, #item-3, #item-4, #item-5, #item-6 {
    display: flex;
    flex-direction: column;
}

#item-1 #text-1, #item-2 #text-2, #item-3 #text-3, #item-4 #text-4, #item-5 #text-5, #item-6 #text-6 {
    text-align: left;
    word-wrap: normal;
    height: 0;
    visibility: hidden;
    opacity: 0;
    padding: 0;
    animation: slow-out 1s ease-in-out 1;
    
}

@keyframes slow-out {
    0% {
        height: 16vh;  
        opacity: 1;
        visibility: visible; 
        padding: 10px; 
    }

    25% {
        opacity: 0;
    }

    100% {
        height: 0;
        opacity: 0;
        visibility: hidden;
        padding: 0;
    }
}


#display {
    display: flex;
    flex-direction: column;
}

#display #text-1, #display #text-2, #display #text-3, #display #text-4, #display #text-5, #display #text-6 {
    
    text-align: left;
    word-wrap: break-word;
    height: 16vh;
    visibility: visible;
    opacity: 1;
    padding: 10px;
    animation: slow-in 1s ease-in-out 1;
}

@keyframes slow-in {

    0% {
        height: 0;
        opacity: 0;
        visibility: hidden;
        padding: 0;
    }
    70% {
        opacity: 0;
    }
    100% {
        height: 16vh; 
        opacity: 1;
        visibility: visible;
        padding: 10px;
    }

}

